import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import CloseIcon from "@material-ui/icons/Close";
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';


function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const styles = theme => ({
    paper: {
        position: 'absolute',
        width: "70%",
        backgroundColor: theme.palette.background.paper,
        // boxShadow: theme.shadows[5],
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingBottom: theme.spacing.unit * 4,
        paddingTop: theme.spacing.unit,
        maxHeight: "80%",
        overflow: "scroll"
    },
});

class SimpleModal extends React.Component {
    state = {
        open: false,
        value: 0,
    };

    handleChange = (event, value) => {
        this.setState({ value });
    };
    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    render() {
        const { classes } = this.props;
        const { value } = this.state;
        return (
            <div className="aboutIcon">
                <span onClick={this.handleOpen} className="butt">O projekte</span>
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                    className="modal"
                >
                    <div style={getModalStyle()} className={classes.paper}>
                        <CloseIcon onClick={this.handleClose} className="closeModal" />
                        <Tabs value={value} onChange={this.handleChange}>
                            <Tab label="Prečo tato webová stránka" />
                            <Tab label="Partneri projektu" />
                        </Tabs>
                        {value === 0 &&
                            <div className="row">
                                <div>
                                    <p>Na predchádzajúcich voľbách do orgánov samosprávy obcí na Slovensku (skrátene komunálne voľby) sa v roku 2014 zúčastnilo iba necelých 50 percent oprávnených voličov a najmä účasť prvovoličov a mladších voličov bola ešte nižšia. </p>
                                    <p>Cieľom tejto webovej stránky je povzbudiť všetkých voličov, ale predovšetkým mladých ľudí, aby využili svoje právo voliť. Informácie zverejňované na stránke sú nadstranícke, nemajú vyjadrovať podporu konkrétnemu kandidátovi, ale zvýšiť záujem ľudí o komunálne voľby a zvýšiť účasť vo voľbách. </p>
                                    <p>Obsah webovej stránky je zabezpečovaný Centrom komunitného organizovania v spolupráci s lokálnymi partnermi. </p>
                                    <p>&nbsp;</p>
                                </div>
                            </div>
                        }
                        {value === 1 &&
                            <div className="row">
                                <div className="col">
                                    <Typography variant="h6">Občianske združenie <br />CENTRUM KOMUNITNÉHO ORGANIZOVANIA (CKO)</Typography> <br /><img src="/images/CKO-logo.jpg" height="100" alt="CENTRUM KOMUNITNÉHO ORGANIZOVANIA" /><br /> <br /> podporuje aktívnu participáciu občanov na procese verejného rozhodovania. Pomáha občanom formulovať a obhajovať ich prirodzené záujmy, zároveň vytvára priestor pre efektívnu spoluprácu medzi občanmi, samosprávou a podnikateľským sektorom s cieľom rozvoja komunít.
                                    <p>Viac o CKO na <a href="http://www.cko.sk" rel="noopener noreferrer" title="CENTRUM KOMUNITNÉHO ORGANIZOVANIA" target="_blank">www.cko.sk</a></p>
                                    <p>&nbsp;</p>
                                </div>
                                <div className="col">
                                    <Typography variant="h6">Kedros, a.s.</Typography>
                                    <p><img src="/images/kedros.png" alt="Kedros a.s." /><br /> Spoločnosť Kedros a.s.  pôsobí na trhu informačných technológií od roku 2004. Ťažiskom našej činnosti je dodávka riešení a poskytovanie služieb v oblasti informačných systémov. Okrem riešení pre komerčné spoločnosti sme technologickým partnerom riešení aj v iných oblastiach ako riešenia pre samosprávy (napr. mapový portál) či pre neziskové organizácie a takto podporujeme zaujímavé projekty v oblasti informatizácie spoločnosti.</p>
                                    <p>Viac o Kedros a.s. na <a href="https://www.kedros.sk" rel="noopener noreferrer" title="Kedros a.s." target="_blank">www.kedros.sk</a></p>
                                </div>
                            </div>
                        }
                    </div>
                </Modal>
            </div>
        );
    }
}

SimpleModal.propTypes = {
    classes: PropTypes.object.isRequired,
};

const AboutModal = withStyles(styles)(SimpleModal);

export default AboutModal;



